import React from "react";
import { Redirect } from "react-router-dom";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "react-lottie";
import Animation1 from "./json/46.json";

import textura from "../../styles/textura.png";

class Step7 extends React.Component {
  componentDidMount() {
    this.startCounting();
  }
  constructor(...args) {
    super(...args);

    this.state = { counter: 0.3, elapsedTime: null };
    this.cincrement = this.cincrement.bind(this);
    this.countUp = this.countUp.bind(this);
    this.startCounting = this.startCounting.bind(this);
  }
  startCounting() {
    setInterval(this.countUp, 1000);
  }
  countUp() {
    this.setState(({ elapsedTime }) => ({ elapsedTime: elapsedTime + 1 }));
  }

  cincrement() {
    this.setState(state => ({ counter: state.counter + 1 }));
  }

  render() {
    const { elapsedTime } = this.state;
    if (elapsedTime === 10) {
      return <Redirect to="/mundocorrienteII" />;
    }
    return (
      <div className="size">
        <div>
          <img
            src={textura}
            onClick={this.cincrement}
            className="textura"
            alt="textura"
          />
        </div>
        <Wizard>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie" onPlayCapture>
                    <Lottie
                      direction={1}
                      speed={this.state.counter}
                      options={{
                        animationData: Animation1,
                        loop: false
                      }}
                    />
                  </div>
                </div>
              )
            }
          </WizardStep>
        </Wizard>
      </div>
    );
  }
}

export default Step7;
