import React from "react";
import { NavLink } from "react-router-dom";
import Lottie from "lottie-react-web";
import paper, { view, Path, Tool } from "paper";

import Animation1 from "./json/1.json";
import textura from "../../styles/textura.png";

class Step8 extends React.Component {
  componentDidMount() {
    paper.install(window);
    window.onload = function() {
      paper.setup("myCanvas");
      view.onResize = function(event) {
        // Whenever the view is resized, move the path to its center:

        // var raster = new Raster(
        //   "http://3.bp.blogspot.com/_fQQ1RUaMzrs/TUiCmVNKPVI/AAAAAAAADks/v1nOGNebYH8/s1600/Mona+Lisa.jpg"
        // );
        // raster.position = view.center;
        var tool = new Tool();

        // raster.opacity = 1;
        tool.fixedDistance = 25;
        tool.onMouseMove = function(event) {
          var path = new Path.Circle({
            center: event.middlePoint,
            radius: 12.5,
            strokeColor: "black"
          });
          path.fillColor = "#00";
        };
      };
    };
  }

  render() {
    return (
      <div className="size">
        <div>
          <NavLink to="/subida" className="button btn btn-primary btn-lg">
            Next
          </NavLink>
        </div>
        <div className="scene">
          <div className="lottie">
            <Lottie
              direction={1}
              speed={1}
              options={{
                animationData: Animation1,
                loop: true
              }}
            />
          </div>
          <div>
            <img src={textura} className="textura" alt="textura" />
          </div>
          <div className="paperscene">
            <canvas className="paper" resize="true" id={"myCanvas"} />
          </div>
        </div>
      </div>
    );
  }
}
export default Step8;
