import React from "react";
import { NavLink } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "lottie-react-web";

import Animation1 from "./json/03.json";
import Animation2 from "./json/04.json";
import textura from "../../styles/textura.png";

class Step2 extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  render() {
    return (
      <div className="size">
        <ReactAudioPlayer
          src="https://upload.wikimedia.org/wikipedia/commons/7/79/Example_of_flats_in_music.ogg"
          volume={1}
          loop={true}
          autoPlay
        />
        <div>
          <img src={textura} className="textura" alt="textura" />
        </div>
        <Wizard>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation1,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation2,
                        loop: true
                      }}
                    />
                  </div>
                  <div className="buttoncontainer">
                    <NavLink to="/mundocorriente" className="yellowbutton">
                      hola
                    </NavLink>
                  </div>
                </div>
              )
            }
          </WizardStep>
        </Wizard>
      </div>
    );
  }
}
export default Step2;
