import React from "react";
import { Redirect } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import { Wizard, WizardStep } from "react-wizard-primitive";
import Lottie from "lottie-react-web";
import Slider from "@material-ui/core/Slider";

import Animation1 from "./json/34.json";
import Animation2 from "./json/35.json";
import Animation3 from "./json/36.json";
import Animation4 from "./json/37.json";
import Animation5 from "./json/38.json";
import Animation6 from "./json/39.json";
import Animation7 from "./json/40.json";
import Animation8 from "./json/41.json";
import Animation9 from "./json/42.json";
import Animation10 from "./json/43.json";
import Animation11 from "./json/44.json";
import Animation12 from "./json/45.json";

import textura from "../../styles/textura.png";

class Step6 extends React.Component {
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  state = {
    slider1: 0
  };

  handleChange = name => (e, value) => {
    this.setState({
      [name]: value
    });
  };
  render() {
    const { slider1 } = this.state;

    function map_range(valor, low1, high1, low2, high2) {
      return low2 + ((high2 - low2) * (valor - low1)) / (high1 - low1);
    }
    if (slider1 === 100) {
      return <Redirect to="/reloj" />;
    }
    return (
      <div className="size">
        <ReactAudioPlayer
          src="https://upload.wikimedia.org/wikipedia/commons/7/79/Example_of_flats_in_music.ogg"
          volume={1}
          loop={true}
          autoPlay
        />
        <div>
          <img src={textura} className="textura" alt="textura" />
        </div>
        <Wizard>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation1,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation2,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation3,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation4,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation5,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation6,
                        loop: true
                      }}
                    />
                  </div>

                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="lottie">
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation7,
                        loop: true
                      }}
                    />
                  </div>
                  <div className="buttoncontainer">
                    <div className="yellowbutton" onClick={nextStep} />
                  </div>
                </div>
              )
            }
          </WizardStep>
          <WizardStep>
            {({ isActive, nextStep }) =>
              isActive && (
                <div className="size">
                  <div className="sliderdiv">
                    <Slider
                      className="slider"
                      value={slider1}
                      aria-labelledby="label"
                      onChange={this.handleChange("slider1")}
                      max={100}
                      min={0}
                    />
                  </div>
                  <div className="lottie" style={{ opacity: 1 }}>
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation8,
                        loop: true
                      }}
                    />
                  </div>
                  <div
                    className="lottie"
                    style={{ opacity: map_range(slider1, 0, 25, 0, 1) }}
                  >
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation9,
                        loop: true
                      }}
                    />
                  </div>
                  <div
                    className="lottie"
                    style={{ opacity: map_range(slider1, 25, 50, 0, 1) }}
                  >
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation10,
                        loop: true
                      }}
                    />
                  </div>
                  <div
                    className="lottie"
                    style={{ opacity: map_range(slider1, 50, 75, 0, 1) }}
                  >
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation11,
                        loop: true
                      }}
                    />
                  </div>
                  <div
                    className="lottie"
                    style={{ opacity: map_range(slider1, 75, 100, 0, 1) }}
                  >
                    <Lottie
                      direction={1}
                      speed={1}
                      options={{
                        animationData: Animation12,
                        loop: true
                      }}
                    />
                  </div>
                </div>
              )
            }
          </WizardStep>
        </Wizard>
      </div>
    );
  }
}
export default Step6;
